* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    outline: none;
}

ul {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: none;
}

button, input {
    background: transparent;
    border: none;
}

textarea {
    resize: none;
    border: none;
}